body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: flex;
  margin: auto;
  background: url('http://www.pepper.ph/wp-content/uploads/2018/02/Candy-Coated-Chocolate-FI.jpg') no-repeat center;
}



.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.chocolate-item{
    list-style-type: none;
    margin-left: 120px;
    margin-right: 120px;
    margin-top: 10px;
    font-family: cursive;
    font-size: 20px;
    border: 5px solid rgba(15, 7, 1, 0.748);
    background-color: rgba(94, 43, 4);
    border-radius: 20px;
    color: rgba(226, 174, 43, 0.974);
}

.chocolate-item button{
    border-color: rgba(15, 7, 1, 0.748);
    background-color: rgba(15, 7, 1, 0.748);
    color:rgba(226, 174, 43, 0.974);
}


.chocolates-list-section {
    width: 27%;
    margin-top: 120px;
    margin-left: 50px;
    color: rgba(226, 174, 43, 0.974);

    
}

.chocolates-list-section h2{
    font-family: cursive;
    font-size: 30px;
    border: 5px solid rgba(15, 7, 1, 0.748);
    background-color: rgba(94, 43, 4, 0.858);
    border-radius: 20px;
    width: 50%;
    margin-left: 110px;
}

.chocolates-list{
    margin-right: 50px;
    color: rgba(226, 174, 43, 0.974);

}

* {
    box-sizing: border-box;
}

.chocolate-box-section {
    width: 40%;
    text-align: center;
    margin-left: 30px;

}
.chocolate-box-section h2{   
    font-family: cursive;
    font-size: 50px;
    border: 5px solid rgba(15, 7, 1, 0.748);
    background-color: rgba(94, 43, 4, 0.858);
    border-radius: 20px;
    color: rgba(226, 174, 43, 0.974);
    margin-left: 0px;
    margin-top: 2px;
}

.chocolate-box {
    width: 640px;
    border: 20px solid rgba(94, 43, 4, 0.858);
    background-color:  rgb(20, 10, 2); 
    border-radius: 20px;
    display: flex;
    flex-wrap: wrap;
}

.chocolate-on-box {
    width: 150px;
    height: 200px;
    border: 1px solid #000;
    border-radius: 20px;
}

.clear-btn{
    font-family: cursive;
    font-size: 20px;
    color: rgba(226, 174, 43, 0.974);
    margin-bottom: 10px;
    background-color: rgb(66, 31, 5);
    border-radius: 20px;
    
}

/* COLORS */
.chocolate-on-box.Black {
    background: #2c1603;
}

.chocolate-on-box.Milk {
    background: #825a39;
}

.chocolate-on-box.Pistachio {
    background: #87b665;
}

.chocolate-on-box.Raspberry {
    background: #e56c73;
}

.chocolate-on-box.White {
    background: #FFFACD;
}
